$Sign: #c24557;
$Exponent: #007abb;
$Hide: #000;
$Mantissa: #039f4e;
$Round_1: #e8db1e;
$Round_2: #c4b9a3;

$true: #1e0155;
$false: #fff;

html {
  height: 100%;
}

body {
  min-height: 100%;
  background: #f5f6fa;
  font-size: 14px !important;
}

img {
  display: block;
}

a {
  text-decoration: none;
  cursor: pointer;
}

input{
  border: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  -o-appearance: none;
  appearance: none;
  outline: none;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 24px #fff inset;
}

button {
  outline: none;
  border: 0;
  background: none;
  cursor: pointer;
}

textarea {
  outline: none;
  resize: none;
}
i,s,u,em,b{
	font-style: normal;
	list-style: none;
  display: inline-block;
  font-weight: normal;
}
input[type="button"],
input[type="submit"],
input[type="reset"],
input[type="file"]::-webkit-file-upload-button,
input[type="search"]::-webkit-search-cancel-button {
  -webkit-appearance: none;
  appearance: none;
}
input,
textarea,
button,
select,
pre,
xmp,
tt,
code,
kbd,
samp {
  line-height: inherit;
  font-family: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
small,
big,
input,
textarea,
button,
select {
  font-size: inherit;
}
ul,
ol,
li {
  list-style: none;
  font-style: normal;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}

.hide{
  display: none;
}
.show{
  display: block;
}
.pages-table-title{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.pages-table-h2{
  font-weight: 600;
  font-size: 18px;
}
.notification-text-style {
  word-wrap: break-word;
  word-break: break-all;
}